.cta-accent2-bg {
    gap: var(--dl-space-space-oneandhalfunits);
    display: flex;
    transform: translate3d(0px, 0px, 0px) scale3d(1, 1, 1) rotateX(0deg) rotateY(0deg) rotateZ(1deg) skew(0deg, 0deg);
    align-self: stretch;
    transition: 0.3s;
    align-items: center;
    border-radius: var(--dl-radius-radius-cardradius);
    justify-content: space-between;
    transform-style: preserve-3d;
    background-color: var(--dl-color-theme-accent2);
  }
  .cta-accent2-bg:hover {
    transform: scale3d(1.1,1.1,1.1);
  }
  .cta-accent1-bg {
    width: 100%;
    display: flex;
    transform: translate3d(0px, 0px, 0px) scale3d(1, 1, 1) rotateX(0deg) rotateY(0deg) rotateZ(-2deg) skew(0deg, 0deg);
    align-items: center;
    border-radius: var(--dl-radius-radius-cardradius);
    justify-content: space-between;
    transform-style: preserve-3d;
    background-color: var(--dl-color-theme-accent1);
  }
  .cta-container2 {
    gap: var(--dl-space-space-threeunits);
    width: 100%;
    display: flex;
    transform: translate3d(0px, 0px, 0px) scale3d(1, 1, 1) rotateX(0deg) rotateY(0deg) rotateZ(1deg) skew(0deg, 0deg);
    transition: 0.3s;
    align-items: center;
    padding-top: var(--dl-space-space-sixunits);
    padding-left: var(--dl-space-space-fourunits);
    border-radius: var(--dl-radius-radius-cardradius);
    padding-right: var(--dl-space-space-fourunits);
    padding-bottom: var(--dl-space-space-sixunits);
  }
  .cta-container2:hover {
    color: var(--dl-color-theme-neutral-light);
    background-color: var(--dl-color-theme-neutral-dark);
  }
  .cta-content {
    gap: var(--dl-space-space-oneandhalfunits);
    display: flex;
    align-items: flex-start;
    flex-direction: column;
  }
  .cta-actions {
    gap: var(--dl-space-space-oneandhalfunits);
    flex: 1;
    display: flex;
    align-items: flex-start;
    justify-content: flex-end;
  }
  @media(max-width: 767px) {
    .cta-container2 {
      gap: var(--dl-space-space-oneandhalfunits);
      flex-direction: column;
      justify-content: flex-start;
    }
  }
  @media(max-width: 479px) {
    .cta-actions {
      flex-wrap: wrap;
      align-self: stretch;
      justify-content: center;
    }
    .cta-button {
      flex: 1;
    }
  }
  